#home {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
   
  width: 80%;
  margin: 0 auto;
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-left: 5%;
  margin-right: 5%;
}

.social-media-icons {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.social-media-icons a {
  margin: 20px 0;
  font-size: 2rem;
  color: #000;
  text-decoration: none;
}

.social-media-icons a:hover {
  color: #0073e6;
  /* Example hover color */
}

.self-introduction {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 5%;
  margin-right: 5%;
}

.text {
  margin-bottom: 10px;
  /* Adjust the spacing between text and photo as needed */
  text-align: center;
  /* Center the text */
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

p {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.hello-button {
  width: 100%;
  height: 20vh;
}

.hello-button svg {
  margin-top: -30px;
}

.cresecent-star1 {
  display: none;
}

.cresecent-moon {
  display: none;
}

.cresecent-star2 {
  display: none;
}

.cresecent-star3 {
  display: none;
}

.cresecent-star4 {
  display: none;
}

.cresecent-star5 {
  display: none;
}

button {
  width: auto;
  height: auto;
  position: relative;
  padding: 0px 35px;
  background-color: #fafafa;
  font-size: 25px;
  cursor: pointer;
  font-weight: 700;
  color: #181818;
  border: 4px solid #000000;
  border-radius: 8px;
  box-shadow: 0 0 0 #292928;
  text-align: center;
  transition: all 0.3s ease-in-out;
}

button:hover {
  animation: hover-stars 1s linear infinite;
  background: transparent;
  color: #686946;
  box-shadow: 0 0 25px #fec1958c;
}

button:hover .cresecent-moon {
  display: block;
  position: absolute;
  filter: drop-shadow(0 0 10px yellow);
  top: 70%;
  left: 90%;
}

button:hover .cresecent-star1 {
  display: block;
  position: absolute;
  filter: drop-shadow(0 0 10px yellow);
  top: 50%;
  left: 70%;
}

button:hover .cresecent-star2 {
  display: block;
  position: absolute;
  filter: drop-shadow(0 0 10px yellow);
  top: 20%;
  left: 50%;
}

button:hover .cresecent-star3 {
  display: block;
  position: absolute;
  filter: drop-shadow(0 0 10px yellow);
  top: 40%;
  left: 30%;
}

button:hover .cresecent-star4 {
  display: block;
  position: absolute;
  filter: drop-shadow(0 0 10px yellow);
  top: 30%;
  left: 15%;
}

button:hover .cresecent-star5 {
  display: block;
  position: absolute;
  filter: drop-shadow(0 0 10px yellow);
  top: 40%;
  left: 5%;
}

.photo-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
   margin-left: 5%;
  margin-right: 5%;
 }

.my-photos {
  width: 400px;
  height: 400px;
  overflow: hidden;
  border-radius: 50%;
  border: 2px solid #000;
  /* Black border around the photo */
  display: flex;
  justify-content: center;
  align-items: center;
}

.photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* Ensures the image fits entirely within the circle */
}

.skills {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.icon {
  font-size: 2rem;
  color: #000;
  margin: auto;
}

.html5 { position: absolute; top: -9%; left: calc(50% - 20px); }
.css3 { position: absolute; top: 15%; left: calc(40% - 160px); }
.js { position: absolute; top: 15%; left: calc(80% + 50px); }
.react { position: absolute; top: 50%; left: calc(60% + 170px); }
.angular { position: absolute; top: 85%; left: calc(60% + 100px); }
.database { position: absolute; top: 85%; left: calc(50% - 180px); }
.java { position: absolute; top: 101%; left: calc(50% - 20px); }
.nodeJs { position: absolute; top: 50%; left: calc(50% - 240px); }

/* Responsive Adjustments for Smaller Devices */

@media (max-width: 768px) {
  #home {
    flex-direction: column;
    padding: 20px 0;
  }

  .sidebar {
    margin-bottom: 20px;
  }

  .self-introduction {
    margin-left: 0;
    text-align: center;
  }

  .photo-container {
    margin-left: 0;
  }

  .my-photos {
    width: 300px;
    height: 300px;
  }

  .icon {
    font-size: 1.5rem;
  }

  h2 {
    font-size: 2rem;
  }

  p {
    font-size: 1.25rem;
  }

  button {
    font-size: 1.5rem;
    padding: 0px 20px;
  }
}

@media (max-width: 480px) {
  .my-photos {
    width: 200px;
    height: 200px;
  }

  .icon {
    font-size: 1.25rem;
  }

  h2 {
    font-size: 1.75rem;
  }

  p {
    font-size: 1rem;
  }

  button {
    font-size: 1.25rem;
    padding: 0px 15px;
  }
}